import * as React from 'react'
import { createRoot } from 'react-dom/client'
import {void_requestSchema, uint_par_responseSchema} from './api_types_pb'
import {ClientBase} from './client_base'
import {useState} from "react";

async function send_request(): Promise<number> {
    const res = await api.send(void_requestSchema, {endpoint: 2}, uint_par_responseSchema)
    console.log(res)
    return res.output
}

const api = new ClientBase()




function Info() {
    const [num_requests, set_num_requests] = useState(0)

    const update_request_number = async () => set_num_requests(await send_request())

    return <div>
        <div><button type="button" onClick={update_request_number}>send request</button></div>
        <div>num requests: {num_requests}</div>
    </div>
}

const domNode = document.getElementById('root');
if (domNode != null) {
    const root = createRoot(domNode);
    root.render(<Info/>);
}

